import React from 'react';
import { Tooltip } from 'antd';
import { ContactList } from '../index';
import './contactIcon.scss';

interface IProp {
  list: ContactList[]
}

const ContactIcon = (props: IProp): JSX.Element => {
  const { list } = props;
  return <div className='icon-group'>
    {
      list.map((item,index) => {
        if (item.src) {
          return <a href={item.src} 
                    key={index}
                    className={`contact_icon ${item.icon}`} 
                    target='_blank'
                  ></a>
        } else {
          return <Tooltip key={index} placement="top" title={item.info}>
            <div className={`contact_icon ${item.icon}`}></div>
          </Tooltip>
        }
      })
    }
  </div>
}

export default ContactIcon;