/**
 *
 * @description 生成项目列表
 * @returns {JSX.Element[]}
 */
function genPj(): JSX.Element[] {
  return projectList.map((item, index) =>
    <div className={`_pj ${item.isImgLeft ? 'img_left' : 'img_right'}`} key={index}>
      <div className={`pj_img ${item.icon}`}></div>
      <div className='pj_info'>
        <div className='_title'>{item.title}</div>
        <div className='time'>{item.time}</div>
        <div className='label'>{item.info}</div>
        <div className='skill'>技术栈:{item.skill}</div>
      </div>
    </div>
  )
}

const Project = (): JSX.Element => {
  return <div id='project'>
    <div className='title'>project experience</div>
    <div className='cmappax'>
      <div className='_title'>cmappax</div>
      <div className='cmappax_img'>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className='cmappax_discription'>
        <p>2019.05 - 至今</p>
        <p>目前公司核心业务，利用canvas技术可对3D地图在2D环境下进行编辑绘制，并实现实时对3D地图更新。其项目包括公司地图管理器桌面端应用，可开放接口编辑器，官网、推广平台、日志以及说明文档网页均由本人独立开发。总共代码约为12w行，项目项目管理部分以及响应式官网使用vue以及electron开发，可对项目数据进行增删改，内部编辑器对标AI进行开发,它使用pixi对canvas 2D图形数据进行新增、绘制、修改、主题皮肤等操作，最后转换为3D地图模型，实现室内地图的绘制以及导航。具体地图效果可以访问路易寻小程序体验。</p>
        <p>技术栈： pixi、vue、electron、gitbook</p>
        <a href="https://www.cmappax.com/cmappax" target='_blank'>前往查看更多</a>
      </div>
    </div>
    <div className='projectList'>
      {
        genPj()
      }
    </div>
  </div>
}

export default Project;