import React from 'react'
import './banner.scss';
import Profile from './profile';
import ContactIcon from './contactIcon';

export interface ContactList {
  name: string,
  icon: string,
  src?: string,
  info?: string,
}

interface IProp {
  isColorWhite: boolean
}

const Banner = (props: IProp): JSX.Element => {

  const { isColorWhite } = props;

  const list: ContactList[] = [
    {
      name: 'github',
      icon: 'icon-github',
      src: 'https://github.com/cangshudada'
    },
    {
      name: '邮箱',
      icon: 'icon-email',
      info: '15342240569@163.com'
    },
    {
      name: '手机',
      icon: 'icon-phone',
      info: '15342240569'
    },
    {
      name: 'QQ',
      icon: 'icon-qq',
      info: '853883909'
    },
    {
      name: 'wechat',
      icon: 'icon-wechat',
      info: 'varon853883909'
    },
  ]

  return <div id='banner'>
    <Profile isColorWhite={isColorWhite} />
    <ContactIcon list={list} />
    LESS IS MORE
  </div>
}

export default Banner;

