import React from 'react';
import { connect } from 'react-redux';
import { stateToProps, dispatchToProps } from '../../store/actions/navMenu';
import { NavMenuReducer } from '../../store/reducer/navMenu';
import './topNav.scss';

interface IProps {
  navMenuList: NavMenuReducer[],
  changeNavMenu: (index: number) => void,
  isShow: boolean
}

/**
 * @param {NavMenuReducer[]} list 需要渲染的menu数组
 * @returns
 */
function genMenu(list: NavMenuReducer[], changeNavMenu: (index: number) => void): JSX.Element[] {

  return list.map((item, index) =>
    <div
      className={`menu ${item.isActive ? '_isMenuActive' : ''}`}
      key={index}
      onClick={() => {
        changeNavMenu(index)
      }}
    >
      {item.name}
    </div>
  )
}

const TopNav = (props: IProps): JSX.Element => {
  const { navMenuList, changeNavMenu, isShow } = props;
  return <div id='top_nav' className={isShow ? '' : 'isHide'}>
    <div className='avatar_logo'></div>
    <div className="nav_menu">
      {
        genMenu(navMenuList, changeNavMenu)
      }
    </div>
  </div>
}


export default connect(stateToProps, dispatchToProps)(TopNav);
