import React from 'react';
import './backTop.scss';
import { CSSTransition } from 'react-transition-group'

interface IProp {
  isShow: boolean,
  onClick: () => void
}

const BackTop = (props: IProp) => {
  const { isShow, onClick } = props;
  return <CSSTransition
    in={isShow}
    timeout={500} 
    classNames='fade'
    unmountOnExit
  >
    <div
      id='back_top'
      onClick={onClick}
    >UP</div>
  </CSSTransition>

}

export default BackTop;