export const scrollAnimate = (container: HTMLInputElement, position: number): void => {
  //想要去的位置的动画
  const scrollToptimer = setInterval(() => {
    const top = container.scrollTop;
    const speed = top / 4;
    if (top !== position) {
      container.scrollTop -= speed;
    } else {
      clearInterval(scrollToptimer);
    }
  }, 18);
}