import { ModifyAction } from '../../actions/navMenu';
import { CHANGE_MENU } from '../../actionType';

export interface NavMenuReducer {
  name: string,
  id: string,
  isActive: boolean
}

interface NavMenuAction {
  type: ModifyAction,
  index: number
}

const defaultState = [
  {
    name: '首页',
    id: 'home',
    isActive: true
  },
  {
    name: '个人',
    id: 'person',
    isActive: false
  },
  {
    name: '项目',
    id: 'project',
    isActive: false
  },
  {
    name: '经历',
    id: 'experience',
    isActive: false
  },
  {
    name: '联系我',
    id: 'contact',
    isActive: false
  },
]

function homeReducer(state: NavMenuReducer[] = defaultState, action: NavMenuAction): NavMenuReducer[] {

  switch (action.type) {

    case CHANGE_MENU: {
      const newState: NavMenuReducer[] = JSON.parse(JSON.stringify(state));
      newState.forEach((item, index) => {
        item.isActive = false;
        if (index === action.index) {
          item.isActive = true;
        }
      });
      return newState;
    }

    default: {
      return state;
    }
  }

}

export default homeReducer;