import React, { useState, useEffect } from 'react';
import './profile.scss';

interface IProp {
  isColorWhite: boolean
}

const Profile = (props: IProp): JSX.Element => {

  const { isColorWhite } = props;

  const [isProfileShow, setProfileShow] = useState(false);

  useEffect(() => {
    setProfileShow(true)
  }, [])

  return <section id='profile' className={`${isProfileShow ? '' : 'profile-close'}`}>
    <div className='profile_avatar'>
      <div
        className='arrow'
        onClick={() => { setProfileShow(!isProfileShow) }}
      ></div>
      <div className='avatar'></div>
    </div>
    <div className={`profileInfo ${isColorWhite ? '_whiteInfo' : ''}`}>
      <div className='name'>KNULP</div>
      <div className='motto'>Doing is better than saying.</div>
    </div>
  </section>
}


export default Profile;
