import React from 'react';
import './aboutMe.scss';


interface Skill {
  name: string,
  className: string
}

const skillList: Skill[] = [
  {
    name: 'typescript',
    className: 'typescript'
  }, {
    name: 'react',
    className: 'react'
  }, {
    name: 'redux',
    className: 'redux'
  }, {
    name: 'vue',
    className: 'vue'
  }, {
    name: 'pixi',
    className: 'pixi'
  }, {
    name: 'electron',
    className: 'electron'
  }, {
    name: '小程序',
    className: 'minip'
  }, {
    name: 'gitbook',
    className: 'gitbook'
  }]


const AboutMe = (): JSX.Element => {

  return <div id='aboutMe'>
    <div className='mask'>关于我</div>
    <div className='title'>ABOUT ME</div>
    <div className='aboutMe'>
      <span>杨启</span>
      <span>前端工程师</span>
      <span>24岁</span>
    </div>
    <div className='skillList'>
      {
        skillList.map((item, index) => (
          <div key={index}>
            <span className={`skillIcon ${item.className}`}></span>
            <span>{item.name}</span>
          </div>
        ))
      }
    </div>
  </div>
}

export default AboutMe;